import React, { Component } from 'react';
import axios from 'axios';
import Iframe from 'react-iframe'
import Divider from '@material-ui/core/Divider';
import InputBase from '@material-ui/core/InputBase';
import ReactPullToRefresh from 'react-pull-to-refresh';
import Bookmark from './components/Bookmark.js';
import SimpleDialogWrapped from './components/SimpleDialogWrapped.js';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      input: "",
      open: false,
      data: [],
      currentUrl: ""
    }
    this.handleClick = this.handleClick.bind(this);
    this.keyPress = this.keyPress.bind(this);
    this.handleRefresh = this.handleRefresh.bind(this);
  }
  handleInputChange = (e) => {
    if(e.key === 'Enter'){
      console.log('enter press here!')
    }
    this.setState({
      input: e.target.value
    })
  }
  keyPress(e){
      if(e.keyCode === 13){
        console.log(e.target.value)

        this.setState({
          currentUrl: e.target.value
        })
      }
  }
  handleClick(item) {
    console.log("clicked");
    // item.update

    // iframe에 넣기
    this.setState({
      currentUrl: item.url,
      data: this.state.data.map(
        ele => ele===item
          ? { ...item, update: 0}
          : ele
      )
    })
    // 서버에 update -> 0 으로 만들어주기
    let url = "http://143.248.140.251:5680/bookmark/visit/";
    let id = item._id.toString()
    axios.put(url+id)
    .then((response) => {
      console.log(response.data);
    })
    .catch((error) => {
      console.log(error);
    });

  }
  componentDidMount() {
    console.log("componentDidMount");
    let url = "http://143.248.140.251:5680/bookmark/all";
    axios.get(url)
    .then((response) => {
      console.log(response.data);
      this.setState({
        data: response.data
      })
    })
    .catch((error) => {
      console.log(error);
    });
  }
  addBookmark() {

  }
  createBookmark() {
    let bookmark_list = [];
    let data = this.state.data;
    // plus button
    bookmark_list.push(
      <Bookmark handleClick={()=>{this.setState({open:true})}}
        isPlus={true}
        key="1"
        id="default" url="default" title1="바로가기" title2="추가" update={2}/>
    )
    // dummy bookmark -> 나중에 수정
    bookmark_list.push(
      <Bookmark handleClick={()=>{}}
        imgsrc="https://yt3.ggpht.com/a-/AAuE7mCIRH83jCASIoa2Y1ZZdagdbShHjgqa3-iQ4g=s288-mo-c-c0xffffffff-rj-k-no"
        key="0"
        id="default" url="default" title1="Madlyn B" title2="Youtube" update={4}/>
    )
    for(let i=0; i<data.length; i++) {
      let item = data[i];
      bookmark_list.push(
        <Bookmark handleClick={()=>this.handleClick(item)}
          key={item._id} imgsrc="http://bokdolfriend.dothome.co.kr/wp-content/uploads/2018/12/븐짜10.jpg"
          id={item._id} url={item.url} title1={item.title1} title2={item.title2} update={item.update}> </Bookmark>
      );
    }
    var bookmark_body = <div style={styles.rowbox}> {bookmark_list} </div>
    return bookmark_body;
  }
  handleRefresh(resolve, reject) {
    // do some async code here
    console.log("리프레싱~!")
    window.location.reload();
  }

  render() {
    return (
      <div className="App" style={styles.container}>
        <ReactPullToRefresh
        onRefresh={this.handleRefresh}
        className="your-own-class-if-you-want"
        style={{
          textAlign: 'center'
        }}>

        <div style={styles.divinput}>
          <InputBase
            style={styles.input}
            placeholder="URL 입력"
            value={this.state.input}
            onChange={this.handleInputChange}
            onKeyDown={this.keyPress}
          />
        </div>

        {this.createBookmark()}

        <SimpleDialogWrapped
          selectedValue={this.state.selectedValue}
          open={this.state.open}
          onClose={()=>{this.setState({open: false})}}
        />

        <Divider variant="middle" />
        <Iframe url={this.state.currentUrl}
        width="100%"
        height="1000px"
        style={styles.iframe}
        id="myId"
        className="myClassname"
        display="initial"
        position="relative"
        allowFullScreen/>
    </ReactPullToRefresh>
      </div>
    );
  }
}

const styles={
  container: {
    display: "flex",
    flexDirection: "column",
    marginTop: "30px",

  },
  divinput: {
    display: "flex",
    justifyContent: "center"
  },
  input:{
    backgroundColor: "#dfdfdf",
    width: "80%",
    borderRadius: 100,
    paddingLeft: 30,
    paddingRight: 10
  },
  rowbox: {
    paddingTop: 10,
    display: 'flex',
    flexDirection: "row"
  },
  iframe: {
    display: 'flex',
    flexGrow: 1
  }
}
export default App;
