import React, { Component } from 'react';
//import axios from 'axios';

import Badge from '@material-ui/core/Badge';
import Avatar from '@material-ui/core/Avatar';

import plusIcon from '../img/plus.png';

class Bookmark extends Component {
  constructor(props) {
    super(props);
    this.state = {
      update: this.props.update // integer
    }
  }
  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.update !== prevState.update) {
      return { update: nextProps.update };
    }
    return null; // null 을 리턴하면 따로 업데이트 할 것은 없다라는 의미

  }
  /*
  handleClick() {
    // client의 badge를 없애고
    this.setState({
      update: 0
    });
    // 서버에 update 를 false 로 만들고
    let url = "http://143.248.140.251:5680/bookmark/visit/";
    let id = this.props.id.toString()
    axios.put(url+id)
    .then((response) => {
      console.log(response.data);
    })
    .catch((error) => {
      console.log(error);
    });
    // 새 창을 연다.
    window.open(this.props.url);
  }
  */

  render() {
    const { title1, title2, handleClick} = this.props;
    const { imgsrc } = this.props || "https://yt3.ggpht.com/a-/AAuE7mCIRH83jCASIoa2Y1ZZdagdbShHjgqa3-iQ4g=s288-mo-c-c0xffffffff-rj-k-no"
    const {isPlus} = this.props || false;
    const _this = this;
    return (
      <div onClick={()=>handleClick()} style={styles.container}>
        {
          isPlus
          ? <div style={styles.badge}>
              <div style={styles.plusdiv}>
                <Avatar style={styles.plus} src={plusIcon} />
              </div>
            </div>
          : <>
            {_this.state.update === 0
            ? <div style={styles.badge}>
                <Avatar style={styles.avatar} src={imgsrc} />
              </div>
            : <Badge style={styles.badge} badgeContent={this.state.update} color="secondary">
                <Avatar style={styles.avatar} src={imgsrc} />
              </Badge>
            }
          </>
        }

        <div style={styles.title}> {title1} </div>
        <div style={styles.title}> {title2} </div>
      </div>
    );
  }
}

const styles = {
  container: {
    width: "20%",
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    margin: "2%",

  },
  badge: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  plusdiv:{
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: '#dfdfdf',
    borderRadius: '50%',
    width: 60,
    height: 60
  },
  plus:{
    width: 20,
    height: 20
  },
  avatar: {

    width: 60,
    height: 60
  },
  title:{
    width: "100%",
    fontSize: 12,
    textAlign: 'center',
  },
}

export default Bookmark;
